<script>
import {_Widget} from "../../common";
import {Downloader} from "../../common";
import {commonConfig} from "../../common";

export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      dates: '',
      tableData:[{dep:'',detail:[{}]}],
      options: [{
        value: 'year',
        label: '年'
      }, {
        value: 'month',
        label: '月'
      }],
      value: '',
      day:[]
    };
  },
  methods: {
    search(){
      var thisObject = this;
      var data = {
        type:'ksyfzl',
        startdate:thisObject.dates
      };
      var url = commonConfig.baseURL + "of_yf_cx";
      Downloader.loadXMLAsync(url + "?as_json=" + JSON.stringify(data), function(e) {
        var xmldoc = e.response;
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          for(var i = 0; i < res.data.length; i++){
            for(var j = 0; j < res.data[i].detail.length; j++){
              var day = res.data[i].detail[j].day;
              res.data[i]["day" + day] =  res.data[i].detail[j].weight;
            }
          }
          thisObject.tableData = res.data;
        }else{
          thisObject.$message({
            type: 'error',
            message: '未查询到相关数据!'
          });
        }
      },function () {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      })
    }
  },
  created() {

  }

}
</script>
